import React, { memo, useEffect, useRef } from "react";
import "amazon-connect-streams";

const instanceName = "tscc-test-instance"

const Ccp = () => {
    const ref = useRef();

    useEffect(() => {
        try {
            console.log("init start");
            if (typeof window === "undefined")
                throw new Error("window missing");
            if (typeof window.connect === "undefined")
                throw new Error("global connect missing");
            window.connect.core.initCCP(ref.current, {
                ccpUrl: `https://${instanceName}.my.connect.aws/connect/ccp-v2`,
                loginPopup: true,
                loginPopupAutoClose: true,
                loginOptions: {                 
                    autoClose: true,              
                    height: 600,                  
                    width: 400,                   
                    top: 0,                       
                    left: 0                       
                  },
                pageOptions: {
                    enableAudioDeviceSettings: true,
                    enablePhoneTypeSettings: true,
                },
                softphone: { allowFramedSoftphone: true },
                ccpAckTimeout: 5000, 
                ccpSynTimeout: 3000, 
                ccpLoadTimeout: 10000

            });
            window.localStorage.removeItem('connectPopupManager::connect::loginPopup');
            console.log("init end");
        } catch (e) {
            console.error(e);
        }
    }, [ref]);
    console.log(ref)
    return (
        <div
            ref={ref}
            style={{
                width: "100%",
                height:"500px"
            }}
        />
    );
};

export default memo(Ccp);
